<template>
    <div>
      <nav-bar title="商品评价" :border="false"></nav-bar>
      <van-tabs v-model="active">
        <van-tab name="-1" :title="`全部(${commentTotal.all})`"></van-tab>
        <van-tab name="10" :title="`好评(${commentTotal.praise})`"></van-tab>
        <van-tab name="20" :title="`中评(${commentTotal.review})`"></van-tab>
        <van-tab name="30" :title="`差评(${commentTotal.negative})`"></van-tab>
      </van-tabs>
      <div class="list" @scroll="pageScroll" ref="listDom" v-if="list.length > 0">
        <div class="item" v-for="item in list" :key="item.comment_id">
            <div class="top">
                <div class="left">
                    <img :src="item.user.avatar_url||defaultAvatar" alt="">
                    <div class="mobile">{{ item.user.nick_name }}</div>
                    <van-rate :value="item.score % 6 + 1" size="16" color="#F4A213" />
                </div>
                <div class="date">{{ item.create_time }}</div>
            </div>
            <div class="bot">
                <div class="text">{{ item.content }}</div>
                <div class="imgs" v-if="item.images.length>0">
                    <img :src="img.image_url" alt="" v-for="img in item.images" :key="img.image_id" @click="imageChagange($event, item.images)">
                </div>
            </div>
        </div>
        <div class="end" v-if="finished">没有更多了</div>
      </div>
      <div class="empty" v-else>
        <van-empty description="暂无内容" />
      </div>
      <img :src="goTopIcon" alt="" :class="{'goTop': true, 'goTop-show': isShowGoTop}" @click="goTop">
    </div>
</template>

<script>
import { ImagePreview } from 'vant'
import defaultAvatar from '@/assets/images/user.jpg'
import List from '@/mixins/list'
export default {
  name: 'GoodsComment',
  mixins: [List],
  data () {
    return {
      defaultAvatar,
      active: '-1',
      commentTotal: {},
      list: [],
      page: 1,
      pageSize: 15,
      pageTotal: 1,
      imgs: [],
      imgIndex: 0
    }
  },
  created () {
    this.getCommentTotal()
    this.getList()
  },
  computed: {
    goodsId () {
      return this.$route.params.id || ''
    }
  },
  watch: {
    active () {
      this.refresh()
      this.getList()
    }
  },
  methods: {
    async getCommentTotal () {
      const { data: { total } } = await this.$api.goods.getCommentTotal(this.goodsId)
      this.commentTotal = total
    },
    async getList () {
      const { data: { list } } = await this.$api.goods.getCommentList(this.active, this.goodsId, this.page)
      const currentList = this.list
      this.list = currentList.concat(list.data)
      this.pageTotal = Math.ceil(list.total / this.pageSize)
      if (this.pageTotal === 1) {
        this.finished = true
      }
    },
    imageChagange (e, imgs) {
      const currentImg = e.target.currentSrc
      this.imgs = []
      imgs.forEach((element, index) => {
        this.imgs.push(element.image_url)
        if (currentImg === element.image_url) {
          this.imgIndex = index
        }
      })
      ImagePreview({
        images: this.imgs,
        startPosition: this.imgIndex
      })
    }
  }
}
</script>

<style lang="less" scoped>
/deep/.van-tab{
    font-size: 15px;
}
/deep/.van-tab--active{
    color: #ee0a24;
    font-weight: bold;
}
.van-tabs{
    position: fixed;
    width: 100%;
    top: 46px;
    left: 0;
    z-index: 100;
}
.list{
    height: calc(100vh - 46px);
    overflow-y: scroll;
    padding-top: 44px;
    background-color: @primaryBg;
    .item{
        padding: 20px 15px 30px;
        border-bottom: 1px solid #f3f2f2;
        background-color: #fff;
        .top{
            display: flex;
            align-items: center;
            justify-content: space-between;
            .left{
                display: flex;
                align-items: center;
                img{
                    width: 25px;
                    height: 25px;
                    border-radius: 50%;
                }
                .mobile{
                    font-size: 14px;
                    color: #999;
                    margin: 0 10px 0 6px;
                }
            }
            .date{
                font-size: 12px;
                color: #999;
            }
        }
        .bot{
            margin-top: 10px;
            .text{
                font-size: 14px;
            }
            .imgs{
                display: flex;
                flex-wrap: wrap;
                img{
                    width: 108px;
                    height: 108px;
                    margin: 15px 9px 0 0;
                    &:nth-of-type(3n){
                        margin-right: 0;
                    }
                }
            }
        }
    }
    .end{
        margin-top: 20px;
    }
}
.empty{
  padding-top: 43px;
  .van-empty{
    height: calc(100vh - 200px);
  }
}
</style>
